export default "@import url(\"https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap\");\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n  font-family: \"Poppins\", sans-serif;\n}\n.hailit2 {\n  height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 10px;\n  background: linear-gradient(135deg, #71b7e6, #9b59b6);\n}\n.container {\n  max-width: 600px;\n  width: 100%;\n  background-color: #fff;\n  padding: 25px 30px;\n  border-radius: 5px;\n  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);\n}\n.container .title {\n  font-size: 30px;\n  font-weight: 600;\n  position: relative;\n}\n.container .title::before {\n  content: \"\";\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  height: 3px;\n  width: 30px;\n  border-radius: 5px;\n  background: linear-gradient(135deg, #71b7e6, #9b59b6);\n}\n.content form .user-details {\n  display: block;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  margin: 20px 0 12px 0;\n}\nform .user-details .input-box {\n  margin-bottom: 15px;\n  width: calc(100% / 2 - 20px);\n}\nform .input-box span.details {\n  display: block;\n  font-weight: 500;\n  margin-bottom: 5px;\n}\n.user-details .input-box input {\n  height: 45px;\n  width: 100%;\n  outline: none;\n  font-size: 16px;\n  border-radius: 5px;\n  padding-left: 15px;\n  border: 1px solid #ccc;\n  border-bottom-width: 2px;\n  transition: all 0.3s ease;\n}\n.user-details .input-box input:focus,\n.user-details .input-box input:valid {\n  border-color: #9b59b6;\n}\nform .gender-details .gender-title {\n  font-size: 20px;\n  font-weight: 500;\n}\nform .category {\n  display: flex;\n  width: 80%;\n  margin: 14px 0;\n  justify-content: space-between;\n}\nform .category label {\n  display: flex;\n  align-items: center;\n  cursor: pointer;\n}\nform .category label .dot {\n  height: 18px;\n  width: 18px;\n  border-radius: 50%;\n  margin-right: 10px;\n  background: #d9d9d9;\n  border: 5px solid transparent;\n  transition: all 0.3s ease;\n}\n#dot-1:checked ~ .category label .one,\n#dot-2:checked ~ .category label .two,\n#dot-3:checked ~ .category label .three {\n  background: #9b59b6;\n  border-color: #d9d9d9;\n}\nform input[type=radio] {\n  display: none;\n}\nform .button {\n  height: 45px;\n  margin: 35px 0;\n}\nform .button input {\n  height: 100%;\n  width: 100%;\n  border-radius: 5px;\n  border: none;\n  color: #fff;\n  font-size: 18px;\n  font-weight: 500;\n  letter-spacing: 1px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  background: linear-gradient(135deg, #71b7e6, #9b59b6);\n}\nform .button input:hover {\n  /* transform: scale(0.99); */\n  background: linear-gradient(-135deg, #71b7e6, #9b59b6);\n}\n@media (max-width: 584px) {\n  .container {\n    max-width: 100%;\n  }\n\n  form .user-details .input-box {\n    margin-bottom: 15px;\n    width: 100%;\n  }\n\n  form .category {\n    width: 100%;\n  }\n\n  .content form .user-details {\n    max-height: 300px;\n    overflow-y: scroll;\n  }\n\n  .user-details::-webkit-scrollbar {\n    width: 5px;\n  }\n}\n@media (max-width: 459px) {\n  .container .content .category {\n    flex-direction: column;\n  }\n}\n.container .option {\n  font-size: 14px;\n  text-align: center;\n}\n.container .facebook a,\n.container .twitter a {\n  display: block;\n  height: 45px;\n  width: 100%;\n  font-size: 15px;\n  text-decoration: none;\n  padding-left: 20px;\n  line-height: 45px;\n  color: #fff;\n  border-radius: 5px;\n  transition: all 0.3s ease;\n}\n.container .facebook i,\n.container .twitter i {\n  padding-right: 12px;\n  font-size: 20px;\n}\n.container .twitter a {\n  background: linear-gradient(to right, #00acee 0%, #1abeff 100%);\n  margin: 20px 0 15px 0;\n}\n.container .twitter a:hover {\n  background: linear-gradient(to left, #00acee 0%, #1abeff 100%);\n  margin: 20px 0 15px 0;\n}\n.container .facebook a {\n  background: linear-gradient(to right, #3b5998 0%, #476bb8 100%);\n  margin: 20px 0 50px 0;\n}\n.container .facebook a:hover {\n  background: linear-gradient(to left, #3b5998 0%, #476bb8 100%);\n  margin: 20px 0 50px 0;\n}\n/*# sourceMappingURL=data:application/json;base64, */"